import React, { memo, useCallback, useState } from 'react';
import {useNavigate} from 'react-router-dom';
import styled from 'styled-components';

import {useQueryString} from '../hooks/useQueryString';
import MenuLink from './MenuLink';

const Form = styled.form`
    position: sticky;
    display: flex;
    top: 0;
    background-color: #fff;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    padding: 10px 0;
    margin: 0;
    margin-bottom: 20px;
    input, button {
        display: block;
        margin: 5px;
        font-size: 16px;
        padding: 0 10px;
        height: 30px;
    }
    input {
    flex: 1;
    max-width: 220px;
    }
    button {
        cursor: pointer;
        width: 70px;
        flex: none;
    }
`;

const Top = memo(() => {
    const navigate = useNavigate();
    const {gte, lte} = useQueryString();
    const [dateQuery, setDateQuery] = useState("");

    const onSubmit = useCallback((e) => {
        e.preventDefault();

        const date1 = e.target.date1.value;
        const date2 = e.target.date2.value;
        const newDateQ = `?gte=${date1}&lte=${date2}`
        setDateQuery(newDateQ);
        navigate(`/confirmed${newDateQ}`);
    }, [navigate]);

    return (
        <div>
            <h1>Covid19 </h1>
            <Form onSubmit={onSubmit}>
                <input type='date' name='date1'/>
                ~
                <input type='date' name='date2'/>
                <button type='submit'></button>
            </Form>
            
            {gte && lte && (
                <nav>
                <MenuLink to={`/confirmed${dateQuery}`}></MenuLink>
                <MenuLink to={`/confirmed_acc${dateQuery}`}></MenuLink>
                <MenuLink to={`/active${dateQuery}`}></MenuLink>
                <MenuLink to={`/released${dateQuery}`}></MenuLink>
                <MenuLink to={`/released_acc${dateQuery}`}></MenuLink>
                <MenuLink to={`/death${dateQuery}`}></MenuLink>
                <MenuLink to={`/death_acc${dateQuery}`}></MenuLink>
                </nav>
            )}
        </div>
    );
});

export default Top;