import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const URL = "http://localhost:3001/covid19";

export const getList = createAsyncThunk(
    "Covid19Slice/getList",
    async ({gte, lte}, {rejectWithValue}) => {
        let result = null;
        try {
            let params={
                date_gte: gte,
                date_lte: lte
            }
            result = await axios.get(URL, {
                params
            });
            console.log(result);
        } catch (err) {
            result = rejectWithValue(err.response);
        }
        return result;
    }
    );
    
const Covid19Slice = createSlice({
    name: 'covid19',
    initialState: {
        data: null,
        loading: false,
        error: null
    },
    reducers: {},
    extraReducers: {
        [getList.pending]: (state, {payload}) => {
            return { ...state, loading: true }
        },
        [getList.fulfilled]: (state, {payload}) => {
            return {
                data: payload?.data,
                loading: false,
                error: null
            }
        },
        [getList.rejected]: (state, {payload}) => {
            return {
                data: null,
                loading: false,
                error: {
                    code: payload?.status ? payload.status : 500,
                    message: payload?.statusText? payload.statuseText : 'Server Error'
                }
            }
        }
    }
});
export default Covid19Slice.reducer;
