import React, { memo } from "react";

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

import { Line } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

// chart data
const LineChartView = memo(({ params, chartData }) => {
  //  
  const options = {
    indexAxis: "x",
    responsive: true,
    plugins: {
      legned: {
        position: "bottom",
      },
    },
  };

  const data = {
    labels: chartData.date,

    datasets: [
      {
        label: "",
        data: chartData[params],
        fill: true,
        backgroundColor: "#0066ff44",
        borderColor: "#0066ff",
      },
    ],
  };

  return <Line data={data} options={options} />;
});

LineChartView.defaultProps = {
  chartData: {
    date: [],
    confirmed: [],
    confirmed_acc: [],
    active: [],
    released_acc: [],
    death: [],
    death_acc: [],
  },
};

export default LineChartView;
