import React, { memo, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useQueryString } from '../hooks/useQueryString';
import { useSelector, useDispatch } from 'react-redux';
import dayjs from 'dayjs';

import { getList } from '../slices/Covid19Slice';
import Spinner from '../components/Spinner';
import ErrorView from '../components/ErrorView';
import LineChartView from '../components/LineChartView';

const Covid19 = memo(() => {
    const {params} = useParams();
    const {gte, lte} = useQueryString();
    // ajax 
    const dispatch = useDispatch();
    const { data, loading, error } = useSelector((state) => state.covid19);
    
    useEffect(() => {
        dispatch(getList({
            gte, lte
        }))
    }, [dispatch, gte, lte]);
   
    const [chartData, setChartData] = useState({});
    useEffect(()=>{
        if(data){
            const newData={
                date: [],
                confirmed: [],
                confirmed_acc: [],
                active: [],
                released: [],
                released_acc: [],
                death: [],
                death_acc: [],
              };
              data.forEach((v,i)=>{
                newData.date.push(dayjs(v.date).format("MM.DD"));
                newData.confirmed.push(v.confirmed);
                newData.confirmed_acc.push(v.confirmed_acc);
                newData.active.push(v.active);
                newData.released.push(v.released);
                newData.released_acc.push(v.released_acc);
                newData.death.push(v.death);
                newData.death_acc.push(v.death_acc);
              });
              setChartData(newData);
            }
        },[data]);
    
    return (
        <div>
            <Spinner visible={loading}/>
            {error? <ErrorView error={error}/> : (
                <LineChartView params={params} chartDate={chartData}/>
            )}
        </div>
    );
});

export default Covid19;